<template>

<div class ="hehe" :class="visible ? 'dropup' : null" >
    <b-button
      :class="visible ? null : 'collapsed'"
      :aria-expanded="visible ? 'true' : 'false'"
      aria-controls="collapse-4"
      @click="visible = !visible"
      class =" dropdown-toggle"
    >
      {{ name }}
    </b-button>
    <b-collapse :id="id" v-model="visible" class="mt-2">
      <b-card :name="id">{{text}}
      <b-button v-on:click="copy(id)"  pill variant="outline-secondary">Copy</b-button>

      </b-card>
    </b-collapse>
  </div>
</template>
<script>
export default {
  props: ["id", "name", "text"],
  name: "Collapse",
  data() {
      return {
        visible: false,
 
      }
    },
    methods:{
      copy: function copyToClipboard(id) {
    var from = document.getElementById(id);
    var range = document.createRange();
    window.getSelection().removeAllRanges();
    range.selectNode(from);
    window.getSelection().addRange(range);
    document.execCommand('copy');
    window.getSelection().removeAllRanges();
}
    }
  

};
</script>

<style scoped>
.hehe{
  margin: 0.3em;
  margin-left: 5%;
  }
  button:not(.btn-outline-secondary){
    width: 95%;
  }
  .btn{
    text-align: left;
    white-space:normal !important;
    word-wrap:break-word;
  }
.card{
  width: 95%;

}
.btn-outline-secondary{
  padding-left: 1em;
  padding-right: 1em;
  margin: 0 45%;
  border-width: 0.2em;
  border-color: #faa307;
}
.btn-outline-secondary:hover{
border-color: #faa307;
background-color: #fcbf49;
}
</style>
